import { useEffect, useState } from "react";
import styled from "styled-components";
import Top from "../static/top.png";

const Wrap = styled.div``;

const TopBtn = styled.img`
  opacity: 0;
  z-index: -10;
  bottom: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: opacity 0.3s ease-in;
  &:hover {
    outline: 0 none;
  }
  &:active {
    outline: 0 none;
  }
  &:focus {
    outline: 0 none;
  }
`;

const TopBtnAct = styled.img`
  z-index: 10;
  opacity: 1;
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: opacity 0.3s ease-in;
`;

const ScrollBtn = () => {
  const [ScrollY, setScrollY] = useState(0);
  const [BtnStatus, setBtnStatus] = useState(false); //  

  const handleFollow = () => {
    setScrollY(window.pageYOffset);
    if (ScrollY > 100) {
      // 100   
      setBtnStatus(true);
    } else {
      // 100   
      setBtnStatus(false);
    }
  };

  const handleTop = () => {
    //     
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    setScrollY(0); // ScrollY   
    setBtnStatus(false); // BtnStatus  false  =>  
  };

  useEffect(() => {
    const watch = () => {
      window.addEventListener("scroll", handleFollow);
    };
    watch();
    return () => {
      window.removeEventListener("scroll", handleFollow);
    };
  });
  return (
    <Wrap>
      {BtnStatus ? (
        <TopBtnAct src={Top} onClick={handleTop} />
      ) : (
        <TopBtn src={Top} />
      )}
    </Wrap>
  );
};

export default ScrollBtn;
